html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

html, body {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: CAIXA Std, sans-serif;
  font-size: 1.6rem;
}

:root {
  --azul-caixa: #005ca9;
  --cinza-caixa: #d0e0e3;
  --laranja-caixa: #f39200;
  --turquesa-caixa: #54bbab;
}

@font-face {
  font-family: CAIXA Std;
  src: url("CAIXAStdVariable.de3aa611.woff2") format("woff2-variations");
}

@font-face {
  font-family: Eras Bold;
  src: url("Eras-Bold-Regular.50098538.ttf");
}
